<template>
  <v-layout wrap justify-center>
       <v-flex xs12 sm6 md12>
        <v-subheader >Grupos</v-subheader>
       </v-flex>
      <v-layout wrap>
          <v-flex xs12 sm6 md3>
            <modal-cadastro-grupo-avaliacao @grupoAvaliacaoCadastrado="cadastroEfetuado = $event"/>
          </v-flex>
      </v-layout>
      <v-container grid-list-md>
          <v-data-table
              :headers="headers" :items="avaliacao.gruposAvaliacoes" class="elevation-1">
                  <template slot="items" slot-scope="props">
                    <td>{{ props.item.numero }}</td>
                    <td>{{ props.item.tema }}</td>
                    <td>
                      <v-icon medium class="mr-2" @click="excluir(props.item)" >
                        delete
                      </v-icon>
                    </td>
                  </template>
                <template slot="no-data">
                    Não existem créditos!
                </template>
             </v-data-table>
          </v-container>
  </v-layout>
</template>
<script>
import avaliacaoService from "@/service/avaliacao/AvaliacaoService";
import EventBus from "@/utils/EventBus";
import ModalCadastroGrupoAvaliacao from "./ModalCadastroGrupoAvaliacao";

export default {
  name: "ListaGrupoAvaliacao",
  props: {
    avaliacao: Object
  },
  components: {
    ModalCadastroGrupoAvaliacao
  },
  data() {
    return {
      cadastroEfetuado: null,
      gruposAvaliacoes: [],
      headers: [
        {
          text: "Número",
          align: "left",
          sortable: false,
          value: "numero"
        },
        {
          text: "Tema",
          align: "left",
          sortable: false,
          value: "tema"
        },
        { text: "Ações", value: "name", sortable: false }
      ]
    };
  },
  watch: {
    cadastroEfetuado(grupoAvaliacao) {
      if (grupoAvaliacao) {
        let grupoRegistrado = null;
        if (this.avaliacao.gruposAvaliacoes.length > 0) {
          grupoRegistrado = this.avaliacao.gruposAvaliacoes.filter(g => {
            return g.tema === grupoAvaliacao.tema && g.numero === parseInt(grupoAvaliacao.numero)
          });
        }
        if (!grupoRegistrado || grupoRegistrado.length === 0) {
          this.avaliacao.gruposAvaliacoes.push(grupoAvaliacao);
        }
      }
    },
    pagination: {
      handler() {
        this.pesquisar();
      }
    }
  },
  methods: {
    excluir(grupoAvaliacao) {
      confirm("Você deseja realmente excluir o Grupo?") &&
        this.avaliacao.gruposAvaliacoes.splice(this.avaliacao.gruposAvaliacoes.indexOf(grupoAvaliacao), 1);
    }
  }
};
</script>
