<template>
<div>
  <section-definition title="Diário" event-update="atualizar"></section-definition>
  <v-toolbar dark color="primary" class="text-xs-center">
      <v-toolbar-title>Gerenciar Diário</v-toolbar-title>
      <v-divider class="mx-2" inset vertical></v-divider>
  </v-toolbar>
  <v-container fluid>
    <v-layout wrap>
      <v-flex xs12 sm6 md3>
        <v-text-field color="primary" disabled v-model="this.$store.getters.user.instituicao"></v-text-field>
      </v-flex>
       <v-flex xs12 sm6 md3>
        <v-select v-model="idCurso" item-text="nome" item-value="id" :items="cursos" label="Curso"></v-select>
      </v-flex>
      <v-flex xs12 sm6 md2>
        <v-select v-model="idPeriodoExecucao" :items="periodosExecucoes" item-text="nome" item-value="id" label="Período"></v-select>
      </v-flex>
      <v-flex xs12 sm6 md1>
        <v-select v-model="idTurma" :items="turmas" item-text="sigla" item-value="id" label="Turma"></v-select>
      </v-flex>
      <v-flex xs12 sm6 md3>
        <v-select v-model="idDisciplina" item-text="sigla" item-value="id" :items="disciplinas"
        label="Disciplina"></v-select>
      </v-flex>
    </v-layout>
    <v-layout row justify-space-between>
      <v-flex xs12 sm12 md4 v-if="idDisciplina">
        <gera-atividades :id-turma="idTurma" :id-disciplina="idDisciplina" />
      </v-flex>
      <v-flex xs12 sm12 md9 v-if="idDisciplina && idAtividade">
        <v-layout wrap>
          <v-flex xs12 sm12 md12 >
            <v-tabs color="primary" dark next-icon="mdi-arrow-right-bold-box-outline" prev-icon="mdi-arrow-left-bold-box-outline" show-arrows>
              <v-tabs-slider color="yellow"></v-tabs-slider>
              <v-tab>Direitos</v-tab>
              <v-tab>Objetivos</v-tab>
              <v-tab>Competências</v-tab>
              <v-tab>Habilidades</v-tab>
              <v-tabs-items>
                    <v-tab-item lazy>
                      <v-card flat>
                        <v-card-text><lista-atividade-disciplina-direito :id-disciplina="idDisciplina"
                        :id-atividade="idAtividade" /></v-card-text>
                      </v-card>
                    </v-tab-item>
                    <v-tab-item lazy>
                      <v-card flat>
                        <v-card-text><lista-atividade-disciplina-objetivo :id-disciplina="idDisciplina"
                        :id-atividade="idAtividade" /></v-card-text>
                      </v-card>
                    </v-tab-item>
                    <v-tab-item lazy>
                      <v-card flat>
                        <v-card-text><lista-atividade-disciplina-competencia :id-disciplina="idDisciplina"
                        :id-atividade="idAtividade" /></v-card-text>
                      </v-card>
                    </v-tab-item>
                    <v-tab-item lazy>
                      <v-card flat>
                        <v-card-text><lista-atividade-disciplina-habilidade :id-disciplina="idDisciplina"
                        :id-atividade="idAtividade" /></v-card-text>
                      </v-card>
                    </v-tab-item>
              </v-tabs-items>
            </v-tabs>
          </v-flex>
          <v-flex xs12 sm12 md12 v-if="idDisciplina && idAtividade">
            <lista-diario :id-atividade="idAtividade" />
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</div>
</template>
<script>
import cursoService from "@/service/curso/CursoService";
import periodoExecucaoService from "@/service/periodoexecucao/PeriodoExecucaoService";
import turmaService from "@/service/turma/TurmaService";
import disciplinaService from "@/service/disciplina/DisciplinaService";
import Pagination from "@/utils/Pagination.js";
import ListaAtividadeDisciplinaDireito from "./ListaAtividadeDisciplinaDireito";
import ListaAtividadeDisciplinaObjetivo from "./ListaAtividadeDisciplinaObjetivo";
import ListaAtividadeDisciplinaCompetencia from "./ListaAtividadeDisciplinaCompetencia";
import ListaAtividadeDisciplinaHabilidade from "./ListaAtividadeDisciplinaHabilidade";
import ListaDiario from "./ListaDiario";
import GeraAtividades from "./GeraAtividades";
import EventBus from "@/utils/EventBus.js";

export default {
  name: "GerenciaCadastroDiario",
  components: {
    GeraAtividades,
    ListaAtividadeDisciplinaDireito,
    ListaAtividadeDisciplinaObjetivo,
    ListaAtividadeDisciplinaCompetencia,
    ListaAtividadeDisciplinaHabilidade,
    ListaDiario
  },
  data() {
    return {
      idAtividade: null,
      idCurso: null,
      cursos: [],
      idPeriodoExecucao: null,
      periodosExecucoes: [],
      idDisciplina: null,
      disciplinas: [],
      idTurma: null,
      turmas: []
    };
  },
  watch: {
    async idCurso() {
      if (this.idCurso) {
        this.periodosExecucoes = await periodoExecucaoService.comboParaModulosDosProfessores(this.idCurso);
      }
      this.idPeriodoExecucao = null;
      this.idTurma = null;
      this.idDisciplina = null;
      this.idAtividade = null;
    },
    async idPeriodoExecucao() {
      if (this.idPeriodoExecucao) {
        this.turmas = await turmaService.comboParaModulosDosProfessores(this.idPeriodoExecucao);
        this.idTurma = null;
        this.idDisciplina = null;
        this.idAtividade = null;
      }
    },
    async idTurma() {
      if (this.idTurma) {
        this.disciplinas = await disciplinaService.comboParaModulosDosProfessores(this.idTurma);
        this.idDisciplina = null;
        this.idAtividade = null;
      }
    },
    pagination: {
      handler() {
        this.pesquisar();
      }
    }
  },
  created() {
    this.carregarDadosBasicos();
     EventBus.$on("selecionaAtividade", async (idAtividade) => {
      this.idAtividade = idAtividade;
    });
  },
  methods: {
    async carregarDadosBasicos() {
      this.cursos = await cursoService.comboParaModulosDosProfessores();
    }
  }
};
</script>
