<template>
<div>
  <modal-cadastro-reposicao @reposicaoRegistrada="cadastroEfetuado = $event"/>
  <v-layout wrap>
    <v-flex xs12 sm6 md2>
      <v-subheader>Frequências</v-subheader>
    </v-flex>
    <v-flex xs12 sm6 md1>
      <v-btn icon @click="atualizar" small>
        <v-icon color="primary">refresh</v-icon>
      </v-btn>
    </v-flex>
    <v-flex xs12 sm12 md12>
      <v-data-table :loading="loading" :headers="headers" :items="frequencias" class="elevation-1" :total-items="pagination.totalItems" :pagination.sync="pagination">
        <template slot="items" slot-scope="props">
          <td>
            <v-tooltip color="transparent" left>
              <span slot="activator" @mouseover="buscarFoto(props.item)">{{props.item.aluno}} - {{props.item.numeroAtividade}} - {{props.item.dataReposicao}}</span>
              <v-avatar :size="100">
              <img :src="foto && foto.dadosBase64 ? foto.dadosBase64 : '/static/doc-images/lists/avatar-default.jpg'" :alt="foto && foto.legenda ? foto.legenda : ''" />
              </v-avatar>
            </v-tooltip>
          </td>
          <td>
            <span v-for="(freq, index) in props.item.frequencia" :key="index">
              <v-menu offset-y>
                <v-btn slot="activator" small icon :color="selecionarCorBotao(freq)">
                  {{freq === 'I' ? '?' : freq}}
                </v-btn>
                <v-list>
                  <v-list-tile v-if="freq === 'P' ? false : true" alt="Presença" @click="atualizarIndividual('P', index, props.item)">
                    <v-list-tile-title>P</v-list-tile-title>
                  </v-list-tile>
                  <v-divider></v-divider>
                  <v-list-tile v-if="freq === 'F' ? false : true" alt="Falta" @click="atualizarIndividual('F', index, props.item)">
                    <v-list-tile-title>F</v-list-tile-title>
                  </v-list-tile>
                  <v-divider></v-divider>
                  <v-list-tile v-if="freq === 'FJ' ? false : true" alt="Falta Justificada" @click="atualizarIndividual('FJ', index, props.item)">
                    <v-list-tile-title>FJ</v-list-tile-title>
                  </v-list-tile>
                  <v-divider></v-divider>
                  <v-list-tile v-if="freq === 'I' ? false : true" alt="Indefinido" @click="atualizarIndividual('I', index, props.item)">
                    <v-list-tile-title>?</v-list-tile-title>
                  </v-list-tile>
                  <v-divider></v-divider>
                </v-list>
              </v-menu>
            </span>
          </td>
          <td>
            <v-menu offset-y>
              <v-btn slot="activator" small icon title="Opções para frequência" color="primary"><v-icon>more_vert</v-icon></v-btn>
              <v-list>
                <v-list-tile alt="TP" @click="criarTipoTP(props.item)">
                  <v-list-tile-title>TP</v-list-tile-title>
                </v-list-tile>
                <v-divider></v-divider>
                <v-list-tile alt="TF" @click="criarTipoTF(props.item)">
                  <v-list-tile-title>TF</v-list-tile-title>
                </v-list-tile>
                <v-divider></v-divider>
                <v-list-tile alt="TF" @click="limpar(props.item)">
                  <v-list-tile-title>Limpar</v-list-tile-title>
                </v-list-tile>
                <v-divider></v-divider>
                <v-list-tile alt="TF" v-if="verificarFrequenciaTodosIndefinidos(props.item)" @click="registrarReposicao(props.item)">
                  <v-list-tile-title>Registrar Reposição</v-list-tile-title>
                </v-list-tile>
                <v-divider></v-divider>
                <v-list-tile alt="TF" v-if="props.item.dataReposicao" @click="anularReposicao(props.item)">
                  <v-list-tile-title>Anular Reposição</v-list-tile-title>
                </v-list-tile>
              </v-list>
            </v-menu>
          </td>
        </template>
        <template slot="no-data">
          Não existem informações cadastradas!
        </template>
      </v-data-table>
    </v-flex>
  </v-layout>
</div>
</template>
<script>
import arquivoService from "@/service/arquivo/ArquivoService";
import frequenciaService from "@/service/frequencia/FrequenciaService";
import Frequencia from "@/model/frequencia/Frequencia";
import Pagination from "@/utils/Pagination.js";
import EventBus from "@/utils/EventBus";
import { limitCaracteres } from "@/filters/Filters.js";
import ModalCadastroReposicao from "./ModalCadastroReposicao";

export default {
  name: "ListaFrequencia",
  props: {
    idAtividade: Number,
    idSubFaseExecucao: Number,
    idDisciplina: Number
  },
  components: {
    ModalCadastroReposicao
  },
  filters: {
    limitCaracteres: limitCaracteres
  },
  data() {
    return {
      visualizaFoto: false,
      foto: null,
      cadastroEfetuado: false,
      dialog: false,
      frequencias: [],
      pagination: new Pagination(1, 5),
      loading: true,
      frequencia: new Frequencia(),
      headers: [
        {
          text: "Alunos",
          align: "left",
          sortable: false
        },
        {
          text: "Frequência",
          align: "left",
          sortable: false,
          value: "frequencia"
        },
        { text: "", value: "name", sortable: false }
      ]
    };
  },
  watch: {
    idAtividade() {
      this.atualizar();
    },
    cadastroEfetuado(event) {
      if (event) {
       this.atualizar();
      }
    },
    pagination: {
      handler() {
        this.pesquisar();
      }
    }
  },
  methods: {
    selecionarCorBotao(freq) {
      switch (freq) {
        case Frequencia.PRESENCA():
          return "success";
        case Frequencia.FALTA():
          return "red accent-3";
        case Frequencia.FALTA_JUSTIFICADA():
          return "purple lighten-3";
        case Frequencia.INDEFINIDO():
          return "deep-purple lighten-3";
        default:
          return "primary";
      }
    },
    async buscarFoto(frequencia) {
      this.foto = await arquivoService.buscarFoto(frequencia.idPessoa);
      this.visualizaFoto = true;
    },
    atualizarIndividual(tipo, index, frequencia) {
      frequencia.frequencia[index] = tipo;
      frequenciaService.atualizarIndividual(frequencia).then((dados) => {
        if (dados) {
          this.atualizar();
        }
      });
    },
    verificarFrequenciaTodosIndefinidos(frequencia) {
      if (frequencia.frequencia && frequencia.frequencia.map) {
        let dadosIndefinidos = [];
        frequencia.frequencia.map(f => {
          if (f === Frequencia.INDEFINIDO()) {
            dadosIndefinidos.push(f);
          }
        });
        return dadosIndefinidos.length === frequencia.numeroAtividade;
      }
      return false;
    },
    criarTipoTP(frequencia) {
      frequenciaService.criarTipoTP(frequencia).then((dados) => {
        if (dados) {
          this.atualizar();
        }
      });
    },
    criarTipoTF(frequencia) {
      frequenciaService.criarTipoTF(frequencia).then((dados) => {
        if (dados) {
          this.atualizar();
        }
      });
    },
    limpar(frequencia) {
      frequenciaService.limpar(frequencia).then((dados) => {
        if (dados) {
          this.atualizar();
        }
      });
    },
    registrarReposicao(frequencia) {
      EventBus.$emit("abrirReposicao", frequencia);
    },
    anularReposicao(frequencia) {
      frequenciaService.anularReposicao(frequencia).then((dados) => {
        if (dados) {
          this.atualizar();
        }
      });
    },
    atualizar() {
      setTimeout(() => {
        this.pesquisar();
        this.cadastroEfetuado = false;
      }, 500);
    },
    async pesquisar() {
      this.loading = true;
      const paginacaoResultado = await frequenciaService.listarTodos(
        this.idDisciplina,
        this.idAtividade,
        this.idSubFaseExecucao,
        this.pagination.page,
        this.pagination.rowsPerPage
      );
      this.frequencias = paginacaoResultado.dados;
      this.pagination.totalItems = paginacaoResultado.total;
      this.loading = false;
    }
  }
};
</script>
