<template>
  <v-layout wrap justify-center>
       <v-flex xs12 sm6 md12>
        <v-subheader >Habilidades</v-subheader>
       </v-flex>
      <v-layout wrap>
          <v-flex xs12 sm6 md3>
            <modal-seleciona-disciplina-habilidade :id-disciplina="idDisciplina"
            @avaliacaoHabilidadeCadastrada="cadastroEfetuado = $event"/>
          </v-flex>
      </v-layout>
      <v-container grid-list-md>
          <v-data-table
              :headers="headers" :items="avaliacao.avaliacoesHabilidades" class="elevation-1">
                  <template slot="items" slot-scope="props">
                    <td>{{ props.item.codigo }}</td>
                    <td width="50%">
                      <v-tooltip bottom>
                          <span slot="activator" class="">{{ props.item.descricao | limitCaracteres(100) }}</span>
                          <span>{{ props.item.descricao }}</span>
                      </v-tooltip>
                    </td>
                    <td>
                      <v-icon medium class="mr-2" @click="excluir(props.item)" >
                        delete
                      </v-icon>
                    </td>
                  </template>
                <template slot="no-data">
                    Não existem créditos!
                </template>
             </v-data-table>
          </v-container>
  </v-layout>
</template>
<script>
import avaliacaoService from "@/service/avaliacao/AvaliacaoService";
import EventBus from "@/utils/EventBus";
import ModalSelecionaDisciplinaHabilidade from "./ModalSelecionaDisciplinaHabilidade";
import { limitCaracteres } from "@/filters/Filters.js";

export default {
  name: "ListaAvaliacaoHabilidade",
  props: {
    avaliacao: Object,
    idDisciplina: Number
  },
  components: {
    ModalSelecionaDisciplinaHabilidade
  },
  filters: {
    limitCaracteres: limitCaracteres
  },
  data() {
    return {
      cadastroEfetuado: null,
      avaliacoesHabilidades: [],
      headers: [
        {
          text: "Código",
          align: "left",
          sortable: false,
          value: "codigo"
        },
        {
          text: "Descrição",
          align: "left",
          sortable: false,
          value: "descricao"
        },
        { text: "Ações", value: "name", sortable: false }
      ]
    };
  },
  watch: {
    cadastroEfetuado(avaliacoesHabilidades) {
      if (avaliacoesHabilidades) {
        if (this.avaliacao.avaliacoesHabilidades.length > 0) {
          avaliacoesHabilidades.map(ah => {
           const habilidadesRegistradas = this.avaliacao.avaliacoesHabilidades.filter(a => {
              return a.descricao === ah.descricao && a.codigo === ah.codigo;
            });
            if (!habilidadesRegistradas || habilidadesRegistradas.length === 0) {
              this.avaliacao.avaliacoesHabilidades.push(ah);
            }
          });

        } else {
          this.avaliacao.avaliacoesHabilidades.push(... avaliacoesHabilidades);
        }
      }
    },
    pagination: {
      handler() {
        this.pesquisar();
      }
    }
  },
  methods: {
    excluir(avaliacaoHabilidade) {
      confirm("Você deseja realmente excluir o Habilidade?") &&
        this.avaliacao.avaliacoesHabilidades.splice(this.avaliacao.gruposAvaliacoes.indexOf(avaliacaoHabilidade), 1);
    }
  }
};
</script>
