import baseService from "../BaseService";
import mixinsMessage from "@/mixins/message";
import ResultadoPesquisa from "@/model/comum/ResultadoPesquisa";

export default {
  listarTodos(
    idDisciplina,
    idAtividade,
    idSubFaseExecucao,
    numeroPagina,
    qtdTotal
  ) {
    return baseService
      .get("/api/frequencias", {
        params: {
          idDisciplina: idDisciplina,
          idAtividade: idAtividade,
          idSubFaseExecucao: idSubFaseExecucao,
          pagina: numeroPagina ? numeroPagina : null,
          total: qtdTotal ? qtdTotal : null
        }
      })
      .then(response => {
        if (response && response.data) {
          return new ResultadoPesquisa(response.data);
        }
        return null;
      })
      .catch(function(error) {
        mixinsMessage.methods.error("Erro ao tentar listar Frequências");
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  criarTipoTP(frequencia) {
    return baseService
      .post("/api/frequencias/tp", frequencia)
      .then(response => {
        mixinsMessage.methods.success(response.data);
        return response.data;
      })
      .catch(function(error) {
        mixinsMessage.methods.error("Erro ao tentar registrar Frequência TP");
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  criarTipoTF(frequencia) {
    return baseService
      .post("/api/frequencias/tf", frequencia)
      .then(response => {
        mixinsMessage.methods.success(response.data);
        return response.data;
      })
      .catch(function(error) {
        mixinsMessage.methods.error("Erro ao tentar registrar Frequência TF");
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  limpar(frequencia) {
    return baseService
      .post("/api/frequencias/limpar", frequencia)
      .then(response => {
        mixinsMessage.methods.success(response.data);
        return response.data;
      })
      .catch(function(error) {
        mixinsMessage.methods.error("Erro ao tentar redefinir a Frequência");
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  registrarReposicao(frequencia) {
    return baseService
      .post("/api/frequencias/registra-reposicao", frequencia)
      .then(response => {
        mixinsMessage.methods.success(response.data);
        return response.data;
      })
      .catch(function(error) {
        mixinsMessage.methods.error(
          "Erro ao tentar registrar reposição da Frequência"
        );
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  anularReposicao(frequencia) {
    return baseService
      .post("/api/frequencias/anula-reposicao", frequencia)
      .then(response => {
        mixinsMessage.methods.success(response.data);
        return response.data;
      })
      .catch(function(error) {
        mixinsMessage.methods.error(
          "Erro ao tentar registrar anulação de reposição da Frequência"
        );
        // eslint-disable-next-line no-console
        console.error(error);
      });
  },
  atualizarIndividual(frequencia) {
    return baseService
      .post("/api/frequencias/atualiza-frequencia-individual", frequencia)
      .then(response => {
        mixinsMessage.methods.success(response.data);
        return response.data;
      })
      .catch(function(error) {
        mixinsMessage.methods.error(
          "Erro ao tentar atualizar Frequência Individual"
        );
        // eslint-disable-next-line no-console
        console.error(error);
      });
  }
};
